<template>
  <section class="home-view-container">
    <h1 class="mb-4">Adopt a new best friend.</h1>
    <p>There're {{ getAllCats.length }} üò∏.</p>
    <article class="mt-3 mb-3">
      <p>There're
      <v-badge
        color="cyan"
        right
      >
        <template v-slot:badge>
          <span>{{ animalsCount }}</span>
        </template>
        <v-icon
          large
          color="grey lighten-1"
        >pets</v-icon>
      </v-badge>
      pets üòΩüê∂.
      </p>
    </article>
    <div>
      Add New Pet
      <v-btn color="success" fab dark @click.stop="petFormModal = true">
        <v-icon>add_circle</v-icon>
      </v-btn>
    </div>
    <v-dialog v-model="petFormModal" max-width="400">
      <v-card>
        <v-card-title class="headline">Add New Pet</v-card-title>
        <v-container fluid>
          <v-form
            width="100%"
            ref="form"
            @submit.prevent="handleSubmit"
            lazy-validation
          >
            <v-text-field
              v-model="formData.name"
              :counter="10"
              label="Pet's Name"
              required
            ></v-text-field>

            <v-text-field
              v-model="formData.age"
              label="Pet's Age"
              required
              type="number"
            ></v-text-field>

            <v-select
              v-model="formData.species"
              :items="['cats','dogs']"
              :rules="[v => !!v || 'Specie is required']"
              label="Specie"
              required
            ></v-select>

            <v-btn
              color="success"
              type="submit"
            >
              Submit
            </v-btn>
          </v-form>
        </v-container>
      </v-card>
    </v-dialog>
  </section>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'home',
  data () {
    return {
      petFormModal: false,
      formData: {
        name: '',
        age: 0,
        species: null
      }
    }
  },
  computed: {
    ...mapGetters([
      'animalsCount',
      'getAllCats'
    ])
  },
  methods: {
    ...mapActions([
      'addPet'
    ]),
    handleSubmit () {
      const { species, age, name } = this.formData
      const payload = {
        species,
        pet: {
          name,
          age
        }
      }
      this.addPet(payload)

      // reset the form after submit
      this.formData = {
        name: '',
        age: 0,
        species: null
      }
    }
  }
}
</script>
